.bird-primary{
    background-color: $green;
    padding: 7px;
    display: block;
    margin: 10px 0;
    color: $dark;
    text-transform: uppercase;
    &:hover{
        color: $cream;
        text-decoration: none;
        box-shadow: 2px 2px 3px #A6A6A6;
    }
    @media(max-width: 480px){
        padding: 3px;
        font-size: 14px;
    }

    span{
        @media(max-width: 480px){
         font-size: 10px;
         padding-bottom: 4px;
        }
    }

}
.bird-primary.submit{
    font-size: 20px;
    padding: 2px 20px;
    width: 150px;
    font-size: 20px;
}
.bird-warning{
    background: $red;
    padding: 7px 0;
    &:hover{
        box-shadow: 2px 2px 3px #A6A6A6;
    }
    input{
        width: 100%;
    }
    input[value]{
        color: $dark;
        background: transparent;
        border: none;
        text-transform: uppercase;
        &:hover{
            color: $cream;
        }
    }
}

.dropdown-menu{
    a{
        background: $dark;
    }
}

#btn-back{
    background: rgba(0,0,0,0);
    border: none;
    span.glyphicon, a{
       color: black;
       font-weight: 100;
       &:hover{
        font-weight: bold;
        text-decoration: none;
    }
    
    }
    
}